import React from "react";
import { useForm } from "react-hook-form";

import "./style.css";
const Login = () => {
  const { login, handleSubmit, watch, errors } = useForm();
  const onSubmit = (data) => console.log(data);
  return (
    <>
      <div className="bg-grey-lighter flex flex-col my-20">
        <div className="container max-w-2xl mx-auto flex-1   border-white flex flex-col items-center justify-center px-2 ">
          <div className=" rounded-3xl bg-white px-20 py-8   shadow-lg text-black w-full my-15">
            <form onSubmit={handleSubmit(onSubmit)}>
              <h1 className="mb-8 text-3xl text-center">SIGN IN</h1>
              <input
                type="email"
                name="email"
                defaultValue="Your Email"
                ref={login}
                className="block border rounded-xl border-grey-light w-full p-3 mb-4"
              />
              <input
                type="password"
                name="password"
                defaultValue="Password"
                ref={login}
                className="block border rounded-xl border-grey-light w-full p-3 mb-4"
              />
              <button
                type="submit"
                className="default-button w-full py-3 mt-6 font-medium tracking-widest text-white bg-black shadow-lg rounded-full focus:outline-none hover:bg-gray-900 hover:shadow-none"
              >
                Log In{" "}
              </button>
            </form>
            <p className="m-8 text-center">
              Don't have an account yet? Register Here
            </p>
          </div>
        </div>
      </div>
    </>
  );
};

export default Login;
